<template>
    <div class="pos">
       <div>
            <el-row >
                <el-col :span='7' class="post-order" id="order-list">
                    <el-tabs>
                        <el-tab-pane label="点餐">
                            <el-table :data="tableData" border style="width:100%;text-align:center;">
                                <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                                <el-table-column prop="count" label="数量" width="65"></el-table-column>
                                <el-table-column prop="price" label="金额" width="65"></el-table-column>
                                <el-table-column label="操作" fixed="right">
                                    <template scope="scope">
                                        <el-button type="text" size="small" @click="delSingleGood(scope.row)">删除</el-button>
                                        <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>     
                                    </template>
                                </el-table-column>
                            </el-table>
                            <div class="total-row">
                                <small>数量:</small>{{totalCount}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                <small>合计:</small>{{totalMoney}}
                            </div>
                            <div class="total-btn">
                                <el-button type="danger" @click="delAllGoods()">删除</el-button>
                                <el-button type="success" @click="checkout()">结账</el-button>
                            </div>   
                        </el-tab-pane>
                        <el-tab-pane label="挂单">
                            挂单
                        </el-tab-pane>
                        <el-tab-pane label="外卖">
                            外卖
                        </el-tab-pane>
                    </el-tabs>
                </el-col>
                <!--商品展示-->
                <el-col :span="17">
                    <div class="often-goods">
                        <div class="title">常用商品</div>
                        <div class="often-goods-list">
                            <ul>
                                <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
                                    <span>{{goods.goodsName}}</span>
                                    <span class="o-price">￥{{goods.price}}元</span>
                                </li>
                            </ul>
                        </div>
                    </div>

                    <div class="goods-type">
                        <el-tabs>
                            <el-tab-pane label="汉堡">
                                <ul class='cookList'>
                                    <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="小食">
                                 <ul class='cookList'>
                                    <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="饮料">
                                 <ul class='cookList'>
                                    <li v-for="goods in type2Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                            <el-tab-pane label="套餐">
                                 <ul class='cookList'>
                                    <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                                        <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                        <span class="foodName">{{goods.goodsName}}</span>
                                        <span class="foodPrice">￥{{goods.price}}元</span>
                                    </li>
                                </ul>
                            </el-tab-pane>
                        </el-tabs>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>


<script>
    import axios from 'axios';
    export default{
        name:'pos',
        data(){
            return{
                tableData:[],
                oftenGoods:[],
                type0Goods:[],
                type1Goods:[],
                type2Goods:[],
                type3Goods:[],
                totalCount:0,
                totalMoney:0
            }
        },
        created:function(){
           
            // axios.get('http://jspang.com/DemoApi/oftenGoods.php').then(function(data){
            //     console.log(data.data);
            //     this.oftenGoods=data.data;
            // }).catch(function(error){
            //     console.log(error)
            // };
            axios.get('http://jspang.com/DemoApi/oftenGoods.php').then(response=>{
                this.oftenGoods=response.data;
            }).catch(error=>{
                console.log(error);
                alert('网络错误，不能访问！')
            })

            axios.get('http://jspang.com/DemoApi/typeGoods.php').then(response=>{
                console.log(response);
                this.type0Goods=response.data[0];
                this.type1Goods=response.data[1];
                this.type2Goods=response.data[2];
                this.type3Goods=response.data[3];

            }).catch(error=>{
                console.log(error);
                alert('网络错误，不能访问！')
            })
        },
        mounted:function(){
            var orderHeight=document.body.clientHeight;
            document.getElementById('order-list').style.height=orderHeight+"px";
        },
        methods:{
            addOrderList(goods){
                this.totalCount=0;
                this.totalMoney=0;
                let isHave=false;
                for(let i=0;i<this.tableData.length;i++){
                    if(this.tableData[i].goodsId==goods.goodsId){
                        isHave=true;
                    }
                }
                if(isHave){
                    let arr=this.tableData.filter(obj=>obj.goodsId==goods.goodsId);
                    arr[0].count++;
                }else{
                    let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
                    this.tableData.push(newGoods);
                }
               
                for(let i=0;i<this.tableData.length;i++){
                    this.totalCount+=this.tableData[i].count;
                    this.totalMoney=this.totalMoney+(this.tableData[i].count*this.tableData[i].price);
                }
            },
            getAllMoney(){
                this.totalCount=0;
                this.totalMoney=0;
                for(let i=0;i<this.tableData.length;i++){
                    this.totalCount+=this.tableData[i].count;
                    this.totalMoney=this.totalMoney+(this.tableData[i].count*this.tableData[i].price);
                }
            },
            delSingleGood(goods){
                this.tableData=this.tableData.filter(obj=>obj.goodsId!=goods.goodsId);
                this.getAllMoney();
            },
            delAllGoods(){
                this.tableData = [];
                this.totalCount = 0;
                this.totalMoney = 0;
            },
            checkout(){
                if(this.totalCount!=0){
                    this.tableData = [];
                    this.totalCount = 0;
                    this.totalMoney = 0;
                    this.$message({
                        message:'结账成功，欢迎下次再来！',
                        type:'success'
                    })
                }else{
                    this.$message.error('你的订单为空，不能空结！')
                }

            }
        }
    }
</script>

<style scoped>
    .post-order{
        background-color:#F9FAFC;
        border-right:1px solid #C0CCDA;
        height:100%;
    }

    .total-btn{
        margin-top:15px;
    }

    .goodsname{
        text-align:center!important;
    }

    .title{
       height: 20px;
       border-bottom:1px solid #D3DCE6;
       background-color: #F9FAFC;
       padding:10px;
   }
   .often-goods-list ul li{
      list-style: none;
      float:left;
      border:1px solid #E5E9F2;
      padding:10px;
      margin:5px;
      background-color:#fff;
   }
  .o-price{
      color:#58B7FF; 
   }

   .goods-type{
       clear:both;
   }

   .cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
 
   }
   .cookList li span{
        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
 
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }

   .total-row{
       background:#fff;
       padding:10px;
       border-bottom:1px solid #D3DCE6;
   }
</style>


